
import { ref, defineComponent , computed , watch } from 'vue'
import { useStore } from 'vuex'
import { useRouter} from 'vue-router'
import $ from 'jquery'
import SmallModal from "@/components/SmallModal.vue"


export default defineComponent({
    setup() {
        //vuex
        const store = useStore();
        //router
        const router = useRouter();
        //language
        const lang_code:any = computed(() => store.state.lang_code);
        //modal;
        const display_modal = ref(1);
        //event list
        const item_list: any= [
            {event: "100公尺 / 100M", id: "100m"},
            {event: "400公尺 / 400M", id: "400m"},
            {event: "1500公尺 / 1500M", id: "1500m"},
            {event: "跳遠 / Long jump", id: "LJ"},
            {event: "鉛球 / Shot put", id: "SP"},
            {event: "標槍 / Javelin throw", id: "JT"},
            {event: "接力 / Relays", id: "RL"},
        ];
        const select_event: any = ref([]);
        const has_relay: any = ref(false);
        const has_individual: any = ref(false);
        watch(select_event, ()=>{
            if(select_event.value.length > 0){
                if(select_event.value.indexOf('RL')==-1){
                    has_relay.value = false;
                    has_individual.value = true;
                }else{
                    has_relay.value = true;
                    has_individual.value = false;
                }
            }else{
                has_relay.value = false;
                has_individual.value = false;
            }
        });
        function getEvent(){
            $.post('https://athletics.nsysu.rikujo.xyz/nsysua_backend/registration/nfac/fetch.php',{token: localStorage.nsysua_token},function(data) {
                select_event.value = JSON.parse(data);
            });
        }
        getEvent();
        //user data
        const user_data: any = computed(() => store.state.user_data);
        //submit
        function submitAll(){
            $.post('https://athletics.nsysu.rikujo.xyz/nsysua_backend/registration/nfac/data.php',{token: localStorage.nsysua_token, sex: user_data.value.sex, event: JSON.stringify(select_event.value)},function(data) {
                if(data!="done"){
                    alert("請聯絡田徑隊");
                    console.log(data);
                }else{
                    alert("完成 Done");
                    router.push('/main/home');
                }
            });
        }
        //delete
        function deleteAll(){
            var r = confirm("確定刪除？\nAre you sure you want to delete?");
            if(r){
                $.post('https://athletics.nsysu.rikujo.xyz/nsysua_backend/registration/nfac/delete.php',{token: localStorage.nsysua_token},function(data) {
                    if(data!="done"){
                        alert("請聯絡田徑隊");
                        console.log(data);
                    }else{
                        alert("完成 Done");
                        getEvent();
                    }
                });
            }
        }
        return {
            lang_code,
            item_list,
            select_event,
            has_relay,
            has_individual,
            submitAll,
            deleteAll,
            display_modal,
        }
    },
    components: {
        SmallModal
    }
})
